<template>
  <div class="hello">
    <h1 style="color:orange">{{ msg }}</h1>
    <br><br><br>
    
    <div >swagger地址:
      <input type="text" v-model ="swaggerUrl" class="input_css" placeholder="如http://127.0.0.1:8080/v2/api-docs"/>
    </div><br>

    <div>选择文档类型:
      <select v-model="docType" aria-placeholder="请选">
        <option value=''>-----------请选择一种想要的文档类型-----------</option>
        <option selected value='Markdown'>Markdown(文件格式为.md的文档))</option>
        <option value='Confluence'>Confluence(文件格式为.txt的wiki文档)</option>
        <option value='AsciiDocs'>AsciiDocs(文件格式为.adoc的文档)</option>
      </select>
    </div><br>
    <!-- <span>Selected: {{ docType }}</span> -->
    <div >
      <button class="btClass" @click="doOut">生成并下载</button>
    </div><br>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Ocean Swagger to Doc',
      docType:'',
      swaggerUrl:'',
    }
  },
  methods:{
    doOut(){
      const docType = this.docType
      const swaggerUrl = this.swaggerUrl
      const reg=/^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\*\+,;=.]+$/;
      if(!reg.test(swaggerUrl)){
          alert("需输入合法的url");
          return;
      }
      if(''===docType){
          alert("需选择文档类型");
          return;
      }
      const docNamePath = swaggerUrl.replace(':','').replace('/','')
      var docName = '';
      if(docType==='Markdown'){
          docName='.md'
      }else if (docType==='Confluence'){
          docName='.txt'
      }else{
          docName='.adoc'
      }
      axios({
        url: '/out',
        method:'post',
        dataType : 'json',
        contentType : 'application/json',
        data:({
          url: swaggerUrl,
          docType: docType
        }),
        responseType: 'blob', 
      }).then(function (response) {
        console.log(response);
        const url = window.URL.createObjectURL(new Blob([response.data]));                
        const link = document.createElement('a');                
        link.href = url;                
        link.setAttribute('download', docNamePath+docName);           
        document.body.appendChild(link); 
        link.click(); 
      }).catch(function (error) {
        console.log(error);
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.input_css{
  width: 290px;
  height: 30px;
  border-bottom: 1px solid #dbdbdb;  
  border-top:0px;  
  border-left:0px;  
  border-right:0px;  
} 
.btClass {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 9px;
    cursor: pointer;
}
.btClass:hover
{
    padding: 12px 20px;
}
</style>



